<template>
  <div>
    <div v-for="(item, id) in results" :key="id">
      <signup-item :signup="item" />
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { api } from '@/utils/api'
export default {
  name: 'Signups',
  data () {
    return {
      results: null
    }
  },
  created () {
    if(!this.authHeader) { 
      this.$router.push('/')
    } 
    api.getdata('/cov/submissions/sid5LL-be', {token: this.authHeader}).then( response => {
      this.results = response.data
    }).catch( err => {
      alert(err)
    })
  },
  computed: {
    ...mapGetters([
      'authHeader'
    ])
  },
}
</script>

