<template>
  <div>
    <h1>Testing Centers</h1>
    <input type="text" v-model="token" placeholder="access code" v-if="!authHeader"/><br />
    <input type="text" v-model="center.name" placeholder="name" /><br />
    <input type="text" v-model="center.address" placeholder="address" /><br />
    <input type="text" v-model="center.city" placeholder="city" /><br />
    <input type="text" v-model="center.state" placeholder="state" /><br />
    <input type="text" v-model="center.zip" placeholder="zip" /><br />
    <input type="text" v-model="center.url" placeholder="url" /><br />
      <input type="text" v-model="center.hours" placeholder="hours" /><br />
      <input type="text" v-model="center.notes" placeholder="notes" /><br />

    <button @click="addItem">Add</button><br />
    <h1>Current</h1>
    <div>
      <div v-for="(item, id) in centers" :key="id">
        <center-item :center="item" @reload="loadLocations"/>
      </div>
    </div> 
  </div>
</template>

<script>
import  { mapGetters, mapActions } from 'vuex'
import { api } from '@/utils/api'
export default {
  name: 'Centers',
  mounted () {
    if(!this.authHeader) { 
      this.$router.push('/')
    } 
    this.loadLocations()
  }, 
  data () {
    return {
      token: null,
      center: {
        name: null,
        address: null,
        city: null,
        state: null,
        zip: null,
        phone: null,
        url: null,
        notes: null
      },
      centers: []
    }
  },
  computed: {
    ...mapGetters([
      'authHeader'
    ])
  },
  methods: {
    ...mapActions([
      'setAuthHeader'
    ]),
    addItem() {
      if(!this.authHeader)
        this.setAuthHeader(this.token)
      api.postdata('/cov/center', this.center, {token: this.authHeader} ).then( response => {
        if(response.data.errors) {
          alert ('failed')
        } else {
          this.loadLocations()
        }
      }).catch( err => {
        alert(err + ' post')
      })
    },
    loadLocations() {
      this.centers = []
      api.getdata('/cov/center').then( response => {
        this.centers = response.data
      }).catch( err => {
        alert(err)
      })
    }
  }
}
</script>

