<template>
  <div class="about">
    <h1>Restaurants</h1>
    <input type="text" v-model="token" placeholder="access code" v-if="!authHeader"/><br />
    <input type="text" v-model="restaurant.name" placeholder="name" /><br />
    <input type="text" v-model="restaurant.address" placeholder="address" /><br />
    <input type="text" v-model="restaurant.city" placeholder="city" /><br />
    <input type="text" v-model="restaurant.state" placeholder="state" /><br />
    <input type="text" v-model="restaurant.zip" placeholder="zip" /><br />
    <input type="text" v-model="restaurant.phone" placeholder="phone" /><br />
    <input type="text" v-model="restaurant.url" placeholder="url" /><br />
    <input type="text" v-model="restaurant.notes" placeholder="notes" /><br />
    <button @click="addItem">Add</button><br />
    <h1>Current</h1>
    <div>
      <div v-for="(item, id) in restaurants" :key="id">
        <p>{{item.name}} <a :href="item.url" target="_blank">{{item.url}}</a> - <button @click="deleteRecord(item._id)">delete</button> - {{item.restId}}</p>
      </div>
    </div> 
  </div>
</template>

<script>
import  { mapGetters, mapActions } from 'vuex'
import { api } from '@/utils/api'

export default {
  name: 'Restaurants',
  mounted () {
    if(!this.authHeader) { 
      this.$router.push('/')
    } 
    this.loadLocations()
  }, 
  data () {
    return {
      token: null,
      restaurant: {
        name: null,
        address: null,
        city: null,
        state: null,
        zip: null,
        phone: null,
        url: null,
        notes: null
      },
      restaurants: []
    }
  },
  computed: {
    ...mapGetters([
      'authHeader'
    ])
  },
  methods: {
    ...mapActions([
      'setAuthHeader'
    ]),
    addItem() {
      if(!this.authHeader)
        this.setAuthHeader(this.token)
      api.postdata('/cov/rest', this.restaurant, {token: this.authHeader} ).then( response => {
        if(response.data.errors) {
          alert ('failed')
        } else {
          this.loadLocations()
        }
      }).catch( err => {
        alert(err + ' post')
      })
    },
    deleteRecord(id) {
      if(!this.authHeader)
        this.setAuthHeader(this.token)
      api.deletedata('/cov/rest', id, {token: this.authHeader} ).then( response => {
        if(!response.data.errors) {
          this.loadLocations()
        } else {
          alert('problems')
        }
      }).catch( err => {
        alert(err + ' post')
      })
    },
    loadLocations() {
      this.restaurants = []
      api.getdata('/cov/rests').then( response => {
        this.restaurants = response.data
      }).catch( err => {
        alert(err)
      })
    }
  }
}
</script>

